html {
  font-family: "Raleway", sans-serif;
  scroll-behavior: smooth;
}

body {
  line-height: 2;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

.container {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

#hero,
#about,
#models,
#gallery {
  margin-bottom: 100px;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("/build/fonts/Raleway-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("/build/fonts/Raleway-Bold.ttf") format("truetype");
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 50px 100px;
}
@media only screen and (max-width: 992px) {
  .header {
    padding: 50px 20px;
  }
}
@media only screen and (max-width: 576px) {
  .header {
    flex-direction: column;
    align-items: center;
    padding: 50px 0;
  }
}

.header__logo {
  font-weight: 300;
  font-size: 30px;
  user-select: none;
  color: #ffffff;
  margin: 0;
}

.header__logo--span {
  font-weight: 900;
}

.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
@media only screen and (max-width: 576px) {
  .nav {
    flex-direction: column;
  }
}

.nav__link {
  color: #ffffff;
  font-size: 20px;
  transition: background-color 0.3s;
  border-radius: 5px;
}
.nav__link:hover {
  background-color: rgba(255, 255, 255, 0.5);
  color: #111111;
}

.hero {
  background-image: url("/build/images/hero.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.hero__slogan {
  text-align: right;
  padding: 70px 100px;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.5);
}
@media only screen and (max-width: 576px) {
  .hero__slogan {
    text-align: center;
    padding: 70px 0;
  }
}

.hero__title {
  font-size: 50px;
}
@media only screen and (max-width: 576px) {
  .hero__title {
    font-size: 30px;
  }
}

.hero__text {
  margin: 0;
  line-height: 1;
  font-size: 22px;
}

.about {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  background-image: url("/build/images/about.webp");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
@media only screen and (max-width: 576px) {
  .about {
    grid-template-columns: auto;
    background-size: cover;
    background-position: -330px 60px;
  }
}

.about__content {
  background-color: #ffffff;
  padding: 50px;
  grid-column: 2/4;
  position: relative;
  top: 30px;
}
@media only screen and (max-width: 576px) {
  .about__content {
    padding: 20px 0;
    position: unset;
  }
}

.models__title {
  font-size: 2.5rem;
  margin: 40px 0;
  text-align: center;
}

.model {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 60px;
}
@media only screen and (max-width: 992px) {
  .model {
    grid-template-rows: unset;
  }
}
@media only screen and (max-width: 576px) {
  .model {
    grid-template-columns: auto;
    grid-template-rows: repeat(2, 1fr);
    background-size: cover;
  }
}

.model__content {
  padding: 50px;
  background-color: #ffffff;
}
@media only screen and (max-width: 576px) {
  .model__content {
    padding: 10px 20px;
  }
}

.model__content--title {
  font-weight: 900;
  text-decoration: underline;
}

.model__content--text {
  margin-bottom: 20px;
}

.model__basic {
  background-image: url("/build/images/models/0.webp");
  background-position: right;
}
@media only screen and (max-width: 576px) {
  .model__basic {
    background-position: center -250px;
  }
}
.model__basic .model__content {
  position: relative;
  bottom: 30px;
}
@media only screen and (max-width: 768px) {
  .model__basic .model__content {
    position: unset;
  }
}

.model__basic .model__content {
  grid-row: 2/3;
  padding: 40px 40px 40px 0;
}

.model__premium {
  background-image: url("/build/images/models/1.webp");
  background-position: left;
}
@media only screen and (max-width: 576px) {
  .model__premium {
    background-position: center -130px;
  }
}
.model__premium .model__content {
  position: relative;
  top: 30px;
}
@media only screen and (max-width: 768px) {
  .model__premium .model__content {
    position: unset;
  }
}

.model__premium .model__content {
  grid-column: 2/3;
  padding: 40px;
}

.model__elite {
  background-image: url("/build/images/models/2.webp");
  background-position: right;
}
@media only screen and (max-width: 576px) {
  .model__elite {
    background-position: center -125px;
  }
}
.model__elite .model__content {
  position: relative;
  top: 50px;
}
@media only screen and (max-width: 768px) {
  .model__elite .model__content {
    position: unset;
  }
}

.model__elite .model__content {
  grid-column: 1/2;
  padding: 40px 40px 40px 0;
}

@media only screen and (max-width: 576px) {
  .model__elite .model__content,
  .model__premium .model__content,
  .model__basic .model__content {
    grid-row: 2/3;
    padding: 10px 0;
  }
}

.gallery__title {
  text-align: center;
}

.gallery__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}
@media only screen and (max-width: 576px) {
  .gallery__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.gallery__image {
  object-fit: cover;
  height: 100%;
  cursor: pointer;
  transition: transform 0.3s;
}

.gallery__image:hover {
  transform: scale(1.02);
}

.gallery__image:nth-child(2) {
  grid-column: 1/2;
  grid-row: 2/4;
}
@media only screen and (max-width: 576px) {
  .gallery__image:nth-child(2) {
    grid-column: 2/3;
    grid-row: 1/3;
  }
}

.gallery__image:nth-child(3) {
  grid-column: 2/3;
  grid-row: 1/2;
}
@media only screen and (max-width: 576px) {
  .gallery__image:nth-child(3) {
    grid-column: 2/3;
    grid-row: 3/5;
  }
}

@media only screen and (max-width: 576px) {
  .gallery__image:nth-child(6) {
    grid-row: 2/4;
  }
}
@media only screen and (min-width: 577px) {
  .gallery__image:nth-child(6) {
    grid-column: 3/4;
    grid-row: 1/3;
  }
}
.overlay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
}
.overlay img {
  display: block;
  max-width: 90vw;
  max-height: 90vh;
  width: auto;
  height: auto;
  object-fit: contain;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.7);
}

.footer {
  height: 500px;
  background-image: url("/build/images/footer.webp");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  color: #ffffff;
}
@media only screen and (max-width: 768px) {
  .footer {
    background-attachment: scroll;
  }
}

.footer__copyright {
  background-color: #111111;
  text-align: center;
  margin: 50px 0 0 0;
  font-size: 15px;
  padding: 10px;
}

.button {
  color: #343A40;
  background-color: #ffffff;
  border: 1px solid #343A40;
  border-radius: 20px;
  padding: 5px 20px;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #343A40;
  color: #ffffff;
}